import $ from 'jquery'

export default () => {
  $(window).on('scroll', () => {
    if ($(window).scrollTop() > 0) {
      $('.header').addClass('fixed')
    } else {
      $('.header').removeClass('fixed')
    }
  })

  if (window.innerWidth < 992) {
    $('.footer-accardion__title').on('click', function toogleAccardion() {
      $(this).toggleClass('active')
      $(this).siblings('.footer-accardion__content').slideToggle()
    })
  }

  $('#image').change(function uploadImages() {
    $("#frames").html('');
    for (let i = 0; i < $(this)[0].files.length; i+= 1) {
      $("#frames").append(`<div class="load-image__image"><img src="${window.URL.createObjectURL(this.files[i])}" width="100px" height="100px"/></div>`)
    }
  })

  $('.checkbox-block__button > label > span').on('click', function openCloseSubFields() {
    const checkbox = $(this).siblings('input')

    if (!checkbox[0].checked) {
      $('.submit-mezcal-bottle').show()
    } else {
      $('.submit-mezcal-bottle').hide()
    }
  })

  $('.account-success-msg__close').on('click', function closeAlert() {
    $(this).parent('.account-success-msg').hide()
  })

  $('.main-textarea').each(function eachTextarea() {
    const dafaultLength = $(this).val().length
    $(this).siblings('.form-label').find('small').find('span').html(dafaultLength)
    $(this).on('load keyup', function handleChange() {
      const msgLength = $(this).val().length
      $(this).siblings('.form-label').find('small').find('span').html(msgLength)
    })
  })

  $('.public-profile__account-settings-button').on('click', function openCloseSettings() {
    $(this).toggleClass('active')
    $('.public-profile__account-settings-list').toggleClass('open')
  })

  $(document).on('click touchstart', (e) => {
    if (!$(e.target).closest('.public-profile__account-settings-button, .public-profile__account-settings-list').length) {
      $('.public-profile__account-settings-button').removeClass('active');
      $('.public-profile__account-settings-list').removeClass('open');
    }
    e.stopPropagation();
  })

  if (window.innerWidth < 768) {
    const flag = $('.public-profile__account-flag')

    $('.public-profile__account-content').append(flag)

    $('.public-profile__product').each(function eachProducts() {
      const head = $(this).find('.public-profile__product-head')
      $(this).find('.public-profile__product-left').append(head)
    })
  }
}


