import $ from 'jquery'
import 'slick-carousel'

export default () => {
  $('.main-section__tags-list').slick({
    slidesToShow: 2,
    slidesToScroll: 1,
    accessibility: false,
    infinite: true,
    arrows: true,
    dots: false,
    rows: 0,
    variableWidth: true,
    nextArrow: '<div class="main-section__tags-arrow arrow-next"><svg width="7" height="10" viewBox="0 0 7 10" fill="none" xmlns="http://www.w3.org/2000/svg"><path opacity="0.4" d="M1 1L5 5L1 9" stroke="#5A5E62" stroke-width="1.5"/></svg></div>'
  })

  $('.brands-about__list').slick({
    slidesToShow: 9,
    slidesToScroll: 1,
    accessibility: false,
    infinite: true,
    arrows: false,
    dots: false,
    rows: 0,
    responsive: [
      {
        breakpoint: 1400,
        settings: {
          slidesToShow: 7,
        }
      },
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 6,
        }
      },
      {
        breakpoint: 1025,
        settings: {
          slidesToShow: 5,
        }
      },
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 4,
        }
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 3,
        }
      },
      {
        breakpoint: 580,
        settings: {
          slidesToShow: 2,
        }
      }
    ]
  })

  $('.products-slider').slick({
    slidesToShow: 2,
    slidesToScroll: 1,
    accessibility: false,
    infinite: true,
    arrows: true,
    dots: false,
    rows: 0,
    variableWidth: true,
    prevArrow: $('.profile-welcome__review-navigation > .prev-arrow').html('<svg width="7" height="10" viewBox="0 0 7 10" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M6 1L2 5L6 9" stroke="#5A5E62" stroke-width="1.5"/></svg>'),
    nextArrow: $('.profile-welcome__review-navigation > .next-arrow').html('<svg width="7" height="10" viewBox="0 0 7 10" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 1L5 5L1 9" stroke="#5A5E62" stroke-width="1.5"/></svg>')
  })

}


